<template>
	<div v-if="lesson.data" class="">
		<header
			class="sticky top-0 z-10 flex items-center justify-between border-b bg-surface-white px-3 py-2.5 sm:px-5"
		>
			<Breadcrumbs class="h-7" :items="breadcrumbs" />
			
		</header>
		<div class="grid md:grid-cols-[70%,30%] h-screen">
			<div
				class="border-r container pt-5 pb-10 px-5">
				<div class="flex flex-col md:flex-row md:items-center justify-between">
					<div class="text-3xl font-semibold text-ink-gray-9">
						{{ lesson.data.lesson_title }}
					</div>
					<div class="flex items-center mt-2 md:mt-0">
						<router-link
							v-if="lesson.data.prev"
							:to="{
								name: 'Lesson',
								params: {
									courseName: courseName,
									chapterNumber: lesson.data.prev.split('.')[0],
									lessonNumber: lesson.data.prev.split('.')[1],
								},
							}"
						>
							<Button class="mr-2">
								<template #prefix>
									<ChevronLeft class="w-4 h-4 stroke-1" />
								</template>
								<span>
									{{ __('Previous') }}
								</span>
							</Button>
						</router-link>
						<router-link
							v-if="user?.data?.is_moderator || is_instructor()"
							:to="{
								name: 'LessonForm',
								params: {
									courseName: courseName,
									chapterNumber: props.chapterNumber,
									lessonNumber: props.lessonNumber,
								},
							}"
						>
							<Button variant="solid" class="mr-2">
								{{ __('Edit') }}
							</Button>
						</router-link>
						<router-link
							v-if="lesson.data.next"
							:to="{
								name: 'Lesson',
								params: {
									courseName: courseName,
									chapterNumber: lesson.data.next.split('.')[0],
									lessonNumber: lesson.data.next.split('.')[1],
								},
							}"
						>
							<Button>
								<template #suffix>
									<ChevronRight class="w-4 h-4 stroke-1" />
								</template>
								<span>
									{{ __('Next') }}
								</span>
							</Button>
						</router-link>
						<router-link
							v-else
							:to="{
								name: 'CourseDetail',
								params: { courseName: courseName },
							}"
						>
							<Button>
								{{ __('Back to Course') }}
							</Button>
						</router-link>
					</div>
				</div>

				<div v-for="instructor in lesson.data.instructors" class="flex items-center">
						<br>
						
					
							
								<InstructorAvatar
									:instructor="instructor"
									size="xl"
								/>
								<br>
							
											
						</div>
				<div
					v-if="
						lesson.data.instructor_content &&
						JSON.parse(lesson.data.instructor_content)?.blocks?.length > 1 &&
						(user?.data?.is_moderator || is_instructor())
					"
					class="bg-surface-gray-2 p-3 rounded-md mt-6"
				>
					<div class="text-ink-gray-5 font-medium">
						{{ __('Instructor Notes') }}
					</div>
					<div
						id="instructor-content"
						class="ProseMirror prose prose-table:table-fixed prose-td:p-2 prose-th:p-2 prose-td:border prose-th:border prose-td:border-outline-gray-2 prose-th:border-outline-gray-2 prose-td:relative prose-th:relative prose-th:bg-surface-gray-2 prose-sm max-w-none !whitespace-normal"
					></div>
				</div>
				<div
					v-else-if="lesson.data.instructor_notes"
					class="ProseMirror prose prose-table:table-fixed prose-td:p-2 prose-th:p-2 prose-td:border prose-th:border prose-td:border-outline-gray-2 prose-th:border-outline-gray-2 prose-td:relative prose-th:relative prose-th:bg-surface-gray-2 prose-sm max-w-none !whitespace-normal mt-6"
				>
					<LessonContent :content="lesson.data.instructor_notes" />
				</div>
				<div v-if="lesson.data.due_date" class="rounded-md bg-[#E6F7F4] p-2 mt-2">
						{{ __('Due: ') + new Intl.DateTimeFormat(user.data.language || 'en-US', { dateStyle: 'medium' }).format(new Date(lesson.data.due_date)) }}
					
				</div>
				<div
					v-if="lesson.data.content"
					class="ProseMirror prose prose-table:table-fixed prose-td:p-2 prose-th:p-2 prose-td:border prose-th:border prose-td:border-outline-gray-2 prose-th:border-outline-gray-2 prose-td:relative prose-th:relative prose-th:bg-surface-gray-2 prose-sm max-w-none !whitespace-normal mt-5"
				>
					<div id="editor"></div>
				</div>
				<div
					v-else
					class="ProseMirror prose prose-table:table-fixed prose-td:p-2 prose-th:p-2 prose-td:border prose-th:border prose-td:border-outline-gray-2 prose-th:border-outline-gray-2 prose-td:relative prose-th:relative prose-th:bg-surface-gray-2 prose-sm max-w-none !whitespace-normal mt-5"
				>
					<LessonContent
						v-if="lesson.data?.body"
						:content="lesson.data.body"
						:youtube="lesson.data.youtube"
						:quizId="lesson.data.quiz_id"
						:exam="lesson.data.exam"
						:assignmentID="lesson.data.assignment_id"
						:discussionID="lesson.data.discussion_id"
						:socket="socket"
						
					/>
				</div>
				<div class="mt-20">
					<Discussions
						v-if="allowDiscussions && lesson.data?.name"
						:title="'Questions'"
						:doctype="'Course Lesson'"
						:docname="lesson.data.name"
						:key="lesson.data.name"
					/>
				</div>
			</div>
			<div class="sticky top-10">
				<div class="bg-surface-menu-bar py-5 px-2 border-b">
					<div class="text-lg font-semibold text-ink-gray-9">
						{{ lesson.data.lesson_title }}
					</div>
					<div
						v-if="user && lesson.data.membership"
						class="text-sm mt-4 mb-2 text-ink-gray-5"
					>
						{{ Math.ceil(lessonProgress) }}% {{ ('completed') }}
					</div>

					<ProgressBar
						v-if="user && lesson.data.membership"
						:progress="lessonProgress"
					/>
				</div>
				<CourseOutline
					:courseName="courseName"
					:key="chapterNumber"
					:getProgress="lesson.data.membership ? true : false"
				/>
			</div>
		</div>
	</div>
</template>
<script setup>
import { createResource, Breadcrumbs, Button } from 'frappe-ui'
import { computed, watch, inject, ref, onMounted, onBeforeUnmount } from 'vue'
import CourseOutline from '@/components/CourseOutline.vue'
import { useRouter, useRoute } from 'vue-router'
import { ChevronLeft, ChevronRight, GraduationCap } from 'lucide-vue-next'
import { getEditorTools, updateDocumentTitle } from '../utils'
import EditorJS from '@editorjs/editorjs'
import LessonContent from '@/components/LessonContent.vue'
import ProgressBar from '@/components/ProgressBar.vue'
import InstructorAvatar from '@/components/InstructorAvatar.vue'
import Discussions from '@/components/Discussions.vue'


const user = inject('$user')
const router = useRouter()
const route = useRoute()
const allowDiscussions = ref(false)
const editor = ref(null)
const instructorEditor = ref(null)
const lessonProgress = ref(0)
const timer = ref(0)
let timerInterval
const socket = inject('$socket')
if (!socket) {
  console.error('Socket connection not found in Lesson.vue.');
} else {
  console.log('Socket connection available in Lesson.vue:', socket);
}

const props = defineProps({
	courseName: {
		type: String,
		required: true,
	},
	chapterNumber: {
		type: String,
		required: true,
	},
	lessonNumber: {
		type: String,
		required: true,
	},
})

onMounted(() => {
	startTimer()
})


const lesson = createResource({
	url: 'seminary.seminary.utils.get_lesson',
	cache: ['lesson', props.courseName, props.chapterNumber, props.lessonNumber],
	makeParams(values) {
		return {
			course: props.courseName,
			chapter: values ? values.chapter : props.chapterNumber,
			lesson: values ? values.lesson : props.lessonNumber,
		}
	},
	auto: true,
	onSuccess(data) {
		if (Object.keys(data).length === 0) {
			router.push({
				name: 'CourseDetail',
				params: { courseName: props.courseName },
			})
			return
		}
		lessonProgress.value = data.membership?.progress
		if (data.content) editor.value = renderEditor('editor', data.content)
		if (
			data.instructor_content &&
			JSON.parse(data.instructor_content)?.blocks?.length > 1
		)
			instructorEditor.value = renderEditor(
				'instructor-content',
				data.instructor_content
			)
	

			editor.value?.isReady.then(() => {
			checkIfDiscussionsAllowed()
		})
	},
})

const renderEditor = (holder, content) => {
	// empty the holder
	if (document.getElementById(holder))
		document.getElementById(holder).innerHTML = ''
	return new EditorJS({
		holder: holder,
		tools: getEditorTools(),
		data: JSON.parse(content),
		readOnly: true,
		defaultBlock: 'embed', // editor adds an empty block at the top, so to avoid that added default block as embed
	})
}
console.log(lesson)
const markProgress = () => {
	if (user.data && lesson.data && !lesson.data.progress) {
		progress.submit()
	}
}

const progress = createResource({
	url: 'seminary.seminary.doctype.course_lesson.course_lesson.save_progress',
	makeParams() {
		return {
			lesson: lesson.data.name,
			course: props.courseName,
		}
		
	},
	onSuccess(data) {
		lessonProgress.value = data
	},
})

const breadcrumbs = computed(() => {
	let items = [{ label: 'Courses', route: { name: 'Courses' } }]
	items.push({
		label: lesson?.data?.course_title,
		route: { name: 'CourseDetail', params: { courseName: props.courseName } },
	})
	items.push({
		label: lesson?.data?.lesson_title,
		route: {
			name: 'Lesson',
			params: {
				courseName: props.courseName,
				chapterNumber: props.chapterNumber,
				lessonNumber: props.lessonNumber,
			},
		},
	})
	return items
})

watch(
	[() => route.params.chapterNumber, () => route.params.lessonNumber],
	(
		[newChapterNumber, newLessonNumber],
		[oldChapterNumber, oldLessonNumber]
	) => {
		if (newChapterNumber || newLessonNumber) {
			editor.value = null
			instructorEditor.value = null
			allowDiscussions.value = false
			lesson.submit({
				chapter: newChapterNumber,
				lesson: newLessonNumber,
			})
			clearInterval(timerInterval)
			timer.value = 0
			startTimer()
		}
	}
)

const startTimer = () => {
	timerInterval = setInterval(() => {
		timer.value++
		if (timer.value == 30) {
			clearInterval(timerInterval)
			markProgress()
		}
	}, 1000)
}

onBeforeUnmount(() => {
	clearInterval(timerInterval)
})



const is_instructor = () => {
	let user_is_instructor = false
	if (lesson.data?.instructors) {
		lesson.data.instructors.forEach((instructor) => {
			if (!user_is_instructor && instructor.user == user.data?.name) {
				user_is_instructor = true
			}
		})
	}
	return user_is_instructor
}

const enrollment = createResource({
	url: 'frappe.client.insert',
	makeParams() {
		return {
			doc: {
				doctype: 'Scheduled Course Roster',
				course: props.courseName,
				member: user.data?.name,
			},
		}
	},
})

const checkIfDiscussionsAllowed = () => {
  console.log('Lesson Data:', lesson.data); // Debugging
  console.log('allow_discuss:', lesson.data?.allow_discuss); // Debugging

  let quizPresent = false;

  // Parse the content and check for blocks
  if (lesson.data?.content) {
    try {
      const parsedContent = JSON.parse(lesson.data.content);
    //   console.log('Parsed Content:', parsedContent); // Debugging

      parsedContent.blocks?.forEach((block) => {
        // Check if the block is of type 'quiz'  and has meaningful data
        if (
          (block.type === 'quiz' ) &&
          block.data &&
          Object.keys(block.data).some((key) => block.data[key] !== null && block.data[key] !== '')
        ) {
          quizPresent = true;
        }
      });
    } catch (error) {
      console.error('Error parsing content:', error);
    }
  }

//   console.log('Quiz  Present:', quizPresent); // Debugging

  // Set allowDiscussions based on conditions
  if ((lesson.data?.allow_discuss === true || lesson.data?.allow_discuss === 1) && !quizPresent) {
    allowDiscussions.value = true;
  } else {
    allowDiscussions.value = false;
  }

//   console.log('allowDiscussions:', allowDiscussions.value); // Debugging
};

const enrollStudent = () => {
	enrollment.submit(
		{},
		{
			onSuccess() {
				window.location.reload()
			},
		}
	)
}

const redirectToLogin = () => {
	window.location.href = `/login?redirect-to=/seminary/courses/${props.courseName}`
}

const pageMeta = computed(() => {
	return {
		title: lesson.data?.lesson_title,
		description: lesson.data?.course_sc,
	}
})

updateDocumentTitle(pageMeta)
const isValidJSON = (str) => {
	try {
		JSON.parse(str);
		return true;
	} catch (e) {
		return false;
	}
}
</script>
